<template>
  <view class="settle-container" :class="shopType === 'food' ? 'food-theme' : 'mall-theme'">
    <!-- 顶部导航 -->
    <view class="settle-header">
      <u-text
        class="title"
        :text="shopType === 'mall' ? '订单结算' : '确认订单'"
        size="36rpx"
        color="#1a1a1a"
        fontWeight="600"
      />
    </view>

    <!-- 配送方式选择 -->
    <view class="delivery-method">
      <view
        class="method-item"
        :class="{ active: deliveryMethod === 'delivery' }"
        @click="deliveryMethod = 'delivery'"
      >
        <u-icon name="car" size="40rpx" :color="deliveryMethod === 'delivery' ? '#ff7d00' : '#999'"/>
        <u-text text="同城配送" align="center" size="28rpx" color="#606266"/>
      </view>
      <view
        class="method-item"
        :class="{ active: deliveryMethod === 'pickup' }"
        @click="deliveryMethod = 'pickup'"
      >
        <u-icon name="bag" size="40rpx" :color="deliveryMethod === 'pickup' ? '#ff7d00' : '#999'"/>
        <u-text text="门店自取" align="center" size="28rpx" color="#606266"/>
      </view>
    </view>

    <!-- 收货地址/自取信息 -->
    <view class="address-card" @click="chooseAddress" v-if="deliveryMethod === 'delivery'">
      <u-icon name="map-fill" color="#999" size="120rpx"/>
      <view class="address-info" v-if="address">
        <u-text
          class="name-phone"
          :text="address.name + ' ' + address.phone"
          size="30rpx"
          color="#1a1a1a"
          fontWeight="600"
          customStyle="line-height:1.4"
        />
        <u-text
          class="address-detail"
          :text="address.province + address.city + address.detail"
          size="26rpx"
          color="#606266"
          customStyle="line-height:1.5;word-break:break-all"
        />
      </view>
      <view class="address-empty" v-else>
        <u-text text="请选择收货地址" size="28rpx" color="#909399"/>
      </view>
      <u-icon name="arrow-right" color="#ccc" size="28rpx"/>
    </view>

    <!-- 自取门店信息 -->
    <view class="pickup-info" v-if="deliveryMethod === 'pickup'">
      <view class="store-info">
        <u-icon name="home" color="#999" size="120rpx"/>
        <view class="store-detail">
          <u-text
            class="store-name"
            text="北京朝阳SOHO店"
            size="32rpx"
            color="#1a1a1a"
            fontWeight="600"
          />
          <u-text
            class="store-address"
            text="朝阳区建国路88号SOHO现代城1层"
            size="26rpx"
            color="#606266"
            customStyle="line-height:1.5;word-break:break-all"
          />
          <u-text
            class="store-phone"
            text="联系电话：010-12345678"
            size="26rpx"
            color="#909399"
          />
        </view>
      </view>

      <view class="pickup-time">
        <u-text text="自取时间：" class="time-label" size="28rpx" color="#1a1a1a"/>
        <picker mode="time" :value="pickupTime" @change="onTimeChange">
          <view class="time-picker">
            <u-text
              :text="pickupTime || '请选择时间'"
              size="28rpx"
              color="#606266"
            />
            <u-icon name="arrow-right" color="#ccc" size="24rpx"/>
          </view>
        </picker>
      </view>
    </view>

    <!-- 商品清单 -->
    <view class="goods-list">
      <view class="list-title">
        <u-text :text="shopType === 'mall' ? '商品清单' : '菜品清单'" size="32rpx" color="#1a1a1a" fontWeight="600"/>
        <u-text class="count" :text="totalCount + '件商品'" size="26rpx" color="#909399"/>
      </view>

      <view class="goods-item" v-for="(item, i) in cartList" :key="i">
        <u-image
          width="120rpx"
          height="120rpx"
          :src="item.cover"
          mode="aspectFill"
          class="goods-img"
        />
        <view class="goods-info">
          <u-text
            class="goods-name"
            :text="item.name"
            size="30rpx"
            color="#1a1a1a"
            fontWeight="500"
            customStyle="line-height:1.4;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden"
          />
          <u-text
            class="goods-spec"
            :text="item.specText"
            size="24rpx"
            color="#909399"
          />
          <view class="goods-price-count">
            <u-text
              class="price"
              :text="'¥' + item.price"
              size="30rpx"
              color="var(--primary-color)"
              fontWeight="600"
            />
            <u-text
              class="count"
              :text="'x' + item.count"
              size="26rpx"
              color="#909399"
            />
          </view>
        </view>
      </view>
    </view>

    <!-- 费用明细 -->
    <view class="fee-detail">
      <view class="fee-item">
        <u-text class="fee-name" text="商品总价" size="28rpx" color="#606266"/>
        <u-text class="fee-value" :text="'¥' + goodsTotal.toFixed(2)" size="28rpx" color="#1a1a1a"/>
      </view>

      <view class="fee-item" v-if="deliveryMethod === 'delivery'">
        <u-text
          class="fee-name"
          :text="shopType === 'food' ? '配送费' : '运费'"
          size="28rpx"
          color="#606266"
        />
        <u-text class="fee-value" :text="'¥' + deliveryCost" size="28rpx" color="#1a1a1a"/>
      </view>

      <view class="fee-item" v-if="deliveryMethod === 'pickup'">
        <u-text class="fee-name" text="自取优惠" size="28rpx" color="#606266"/>
        <u-text class="fee-value" text="-¥5.00" size="28rpx" color="#ff7d00"/>
      </view>

      <view class="fee-item">
        <u-text class="fee-name" text="优惠" size="28rpx" color="#606266"/>
        <u-text
          class="fee-value"
          :text="'-¥' + discount.toFixed(2)"
          size="28rpx"
          color="#ff7d00"
        />
      </view>

      <view class="fee-item total-fee">
        <u-text class="fee-name" text="实付款" size="32rpx" color="#1a1a1a" fontWeight="600"/>
        <u-text
          class="fee-value"
          :text="'¥' + finalAmount.toFixed(2)"
          size="36rpx"
          color="var(--primary-color)"
          fontWeight="700"
        />
      </view>
    </view>

    <!-- 底部提交栏 -->
    <view class="submit-bar">
      <view class="total-amount">
        <u-text class="label" text="实付：" size="28rpx" color="#606266"/>
        <u-text
          class="amount"
          :text="'¥' + finalAmount.toFixed(2)"
          size="40rpx"
          color="var(--primary-color)"
          fontWeight="700"
        />
      </view>
      <u-button
        :text="deliveryMethod === 'delivery' ? '提交订单' : '确认自取'"
        type="primary"
        :color="shopType === 'mall'
          ? 'linear-gradient(90deg, #ff4b7e 0%, #ff3366 100%)'
          : 'linear-gradient(90deg, #ff7d00 0%, #ff5700 100%)'"
        @click="submitOrder"
      />
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      shopType: 'food', // 'mall'/'food'
      deliveryMethod: 'delivery', // 'delivery'/'pickup'
      pickupTime: '', // 自取时间
      address: {
        name: '张先生',
        phone: '138****6789',
        province: '北京市',
        city: '朝阳区',
        detail: '建国路88号SOHO现代城3号楼2001室'
      },
      cartList: [
        {
          id: 1,
          name: '招牌麻辣香锅',
          price: 68,
          cover: '/static/bg.png',
          specText: '中辣 · 加香菜',
          count: 1
        },
        {
          id: 2,
          name: '冰柠檬水',
          price: 8,
          cover: '/static/swiper-background-1.png',
          specText: '常温 · 少冰',
          count: 2
        }
      ],
      deliveryFee: 5,
      freight: 10,
      discount: 10,
      pickupDiscount: 5 // 自取优惠
    }
  },
  computed: {
    totalCount() {
      return this.cartList.reduce((sum, item) => sum + item.count, 0)
    },
    goodsTotal() {
      return this.cartList.reduce((sum, item) => sum + item.price * item.count, 0)
    },
    deliveryCost() {
      if (this.deliveryMethod === 'pickup') return 0
      if (this.shopType === 'food') return this.deliveryFee
      return this.goodsTotal >= 200 ? 0 : this.freight
    },
    finalAmount() {
      let amount = this.goodsTotal + this.deliveryCost - this.discount
      if (this.deliveryMethod === 'pickup') {
        amount -= this.pickupDiscount
      }
      return Math.max(amount, 0)
    }
  },
  methods: {
    chooseAddress() {
      uni.navigateTo({ url: '/pages/address/address' })
    },
    onTimeChange(e) {
      this.pickupTime = e.detail.value
    },
    submitOrder() {
      if (this.deliveryMethod === 'delivery' && !this.address) {
        uni.showToast({ title: '请选择收货地址', icon: 'none' })
        return
      }
      if (this.deliveryMethod === 'pickup' && !this.pickupTime) {
        uni.showToast({ title: '请选择自取时间', icon: 'none' })
        return
      }
      uni.showLoading({ title: '提交中...' })
      setTimeout(() => {
        uni.hideLoading()
        uni.showToast({
          title: this.deliveryMethod === 'delivery' ? '订单提交成功' : '自取订单已创建',
          icon: 'success'
        })
        setTimeout(() => {
          uni.navigateTo({ url: '/pages/order-list/order-list' })
        }, 1000)
      }, 1500)
    }
  }
}
</script>

